// import detectEthereumProvider from '@metamask/detect-provider';
import Web3 from 'web3';
import { ETH_NETWORK } from './constants';
import ctx from '../index';
import { showConfirm } from '../../components/Modal';
import { getBtcbAmount, getWbnbAmount, getBTokenAmount, isApproveCol, isApprovePaired, getEthAmount } from './transaction';
import { getBumperAmount, getLpAmount, getStakeLpAmount, getLpTotalSupply, isApproveLP, getLpReserves } from './farmTransaction';
import { getBtcbPrice, getBcTokenPrice, getBumperPrice, getEthPrice } from './price';
import { getLpApy, getLendApy, getBorrowApy } from './apy';

// 初始化metamask，初始化链上数据
export const initChain = async () => {
  // 异步加载 @metamask 优化体积
  const detectEthereumProvider = await import(
    /* webpackChunkName: 'metamask' */
    '@metamask/detect-provider'
  ).then(m => m.default);
  // Detect the MetaMask Ethereum provider
  // this returns the provider, or null if it wasn't detected
  const provider = await detectEthereumProvider();
  if (provider) {
    if (provider === window.ethereum) {
      ctx.data.chainProvider = provider;
      const network = await provider.request({
        method: 'net_version',
        params: [],
      });
      console.log('current network is:', ETH_NETWORK[network]);
      // 注册web3 provider
      ctx.data.web3 = new Web3(provider);
    } else {
      showConfirm({
        title: 'Please only use MetaMask!',
        content: 'Do you have multiple wallets installed?',
        onOk() {
          window.location.href = 'https://metamask.io/download.html';
        }
      });
    }
  } else {
    // ctx.data.web3 = new Web3.providers.HttpProvider("http://localhost:8545");
    showConfirm({
      title: 'Please install MetaMask!',
      content: 'You have not install MetaMask, jump link to install?',
      onOk() {
        window.location.href = 'https://metamask.io/download.html';
      }
    });
  }
};

// 更新账号信息
export const updateAccount = async (account) => {
  console.log('updateAccount ......');
  if (account && account.length) {
    const chainAccount = account[0];
    ctx.data.chainAccount = chainAccount;
    // paired
    await getWbnbAmount(chainAccount);
    await getBumperAmount(chainAccount); // 获取bumper的数目
    // approve
    await isApproveCol(); // 是否授权了btcb
    await isApprovePaired(); // 是否授权了wbnb
    await isApproveLP(); // 是否授权了lp
    // btcb
    await getBtcbAmount(chainAccount);
    await getBTokenAmount('BTCB',chainAccount);
    await getLpAmount('BTCB',chainAccount); // 获取btcb对应的btoken生成的lp的数目
    await getStakeLpAmount('BTCB',chainAccount);
    // eth
    await getEthAmount(chainAccount);
    await getBTokenAmount('ETH',chainAccount);
    await getLpAmount('ETH',chainAccount); // 获取btcb对应的btoken生成的lp的数目
    await getStakeLpAmount('ETH',chainAccount);
    await getLpAmount('ETH',chainAccount); // 获取btcb对应的btoken生成的lp的数目
    await getStakeLpAmount('ETH',chainAccount);
  } else {
    ctx.data.chainAccount = 0;
  }
}

// 更新supply总量的数据
export const updateSupply = async () => {
  console.log('updateSupply ......');
  await getLpTotalSupply('BTCB');// 获取lp totalSupply
  await getLpReserves('BTCB');
  await getLpTotalSupply('ETH');// 获取lp totalSupply
  await getLpReserves('ETH');
}

// 更新价格
export const updatePrice = async () => {
  console.log('updatePrice ......');
  // btcb
  await getBtcbPrice();
  await getBcTokenPrice('BTCB');
  // eth
  await getEthPrice();
  await getBcTokenPrice('ETH');
  // bumper
  await getBumperPrice();
}

// 更新价格
export const updateApy = async () => {
  console.log('updateApy ......');
  await getLpApy('BTCB');
  await getLpApy('ETH');
  await getLendApy('BTCB');
  await getLendApy('ETH');
  await getBorrowApy('BTCB');
  await getBorrowApy('ETH');
}